import Vue from 'vue'
import Router from 'vue-router'
import indexMain from '@/components/home/Main'
import introMain from '@/components/intro/Main'
import recordMain from '@/components/record/Main'
import contactMain from '@/components/contact/Main'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'indexMain',
      component: indexMain
    },
    {
      path: '/intro',
      name: 'introMain',
      component: introMain
    },
    {
      path: '/contact',
      name: 'contactMain',
      component: contactMain
    },
    {
      path: '/record',
      name: 'recordMain',
      component: recordMain
    }
  ]
})

export default router



