<template>
  <div>
    <transition name="el-fade-in-linear">
      <div v-show="show" class="v-main-container">
        intro
        <nav-bar></nav-bar>
      </div>
    </transition>
  </div>
</template>

<script>
import navBar from '../../components/nav/navBar'
export default {
  name: 'introMain',
  components: {
    navBar
  },
  mounted () {
    this.show = true
  },
  data () {
    return {
      show: false
    }
  }
}
</script>

<style scoped lang="scss">

@import "../../style/entry";


</style>



