<template>
  <div class="v-main-container">
    <transition name="el-fade-in-linear">
      <div class="main" v-show="show">
        <div class="center">
          <div class="wave-wrap"><div class="wave"></div></div>
          <img src="../../assets/logoko.png" />        
        </div>
          <h1 v-show="show" class="animated fadeInUp">1995-01-07</h1>
          <h1 v-show="show" class="animated fadeInUp">HeFei<i class="rhombus"></i>China</h1>
          <nav-bar></nav-bar>
          <main-canvas :dotsNum="dotsNum" ></main-canvas>
      </div>
    </transition>
  </div>
</template>

<script>
import mainCanvas from './mainCanvas'
import navBar from '../../components/nav/navBar'
export default {
  name: 'indexMain',
  components: {
    mainCanvas,
    navBar
  },
  beforeCreate: function () {
    this.dotsNum = /Android|webOS|iPhone|iPod|BlackBerry/i.test(
      navigator.userAgent
    )
      ? 20
      : 60
  },
  mounted: function () {
    this.show = true
  },
  data () {
    return {
      roundColor: '#999',
      lineColor: '',
      isColor: true,
      show: false
    }
  }
}
</script>

<style lang="scss">
@import "../../style/entry";
</style>

<style lang="scss" scoped>
@import "../../style/entry";
.main {
  text-align: center;
  box-sizing: border-box;
  padding-top: 16%;
}



h1 {
  text-align: right;
  width: 2rem;
  margin: auto;
  font-size: 0.2rem;
  line-height: 0.3rem;
}

.rhombus {
  display: inline-block;
  margin: 0 0.08rem 0.03rem 0.08rem;
  border-style: solid;
  border-width: 0.03rem;
  width: 0;
  height: 0;
  -moz-transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

.animated {
  animation-duration: 2s;
  -webkit-animation-duration: 2s;
}

.center{
  position: relative;
  width: 2.8rem;
  height: 1.8rem;
  margin: auto;
  img{
     width: 2.8rem;
     position: absolute;
     left: 0;
     z-index: 25;
  }
}
.wave-wrap{
  position: absolute;
  right: .27rem;
  top: .27rem;
}
.wave {
    position: relative;
    width: 1.1rem;
    height: 1.1rem;
    background-color: #a6d0e1;
    border-radius: 50%;
  
    &::before,
    &::after{
        content: "";
        position: absolute;
        width: 2.2rem;
        height: 2.2rem;
        top: 0;
        left: 50%;
        background-color: rgba(255, 255, 255, .8);
        border-radius: 45%;
        transform: translate(-50%, -70%) rotate(0);
        animation: rotate 6s linear infinite;
    }
     
    &::after {
        border-radius: 47%;
        transform: translate(-50%, -70%) rotate(0);
        animation: rotate 10s linear -5s infinite;
    }
}
 
@keyframes rotate {
    50% {
        transform: translate-(52%, -73%) rotate(180deg);
    } 100% {
        transform: translate(-50%, -70%) rotate(360deg);
    }
}

</style>



