<template>
  <div class="v-main-container">
    <header>
      <img class="logo" src="../../assets/logo-black.png" />  
    </header>
    <div class="main">
      <h2>目前供职</h2>
      <h1>科大讯飞开放平台研发部</h1>
      <h3>地址 合肥市望江西路666号 A1 14F</h3>
      <h3>邮箱 caocaochigua@outlook.com</h3>
      <div class="icons">
