        </el-tooltip>
  
         <el-tooltip class="item" effect="dark" content="MinminTemur#5990" placement="bottom">
         <a><i class="battle"></i></a> 
        </el-tooltip> 
      </div>
  </div> 
     <nav-bar></nav-bar>
  </div>
</template>

<script>
import navBar from '../../components/nav/navBar'
export default {
  name: 'contactMain',
  components: {
    navBar
  },
  data () {
    return {}
  }
}
