    font-size: .16rem;
    color: #4d4d4d;
  }
  .icons{
    margin: .2rem 0;
    i{
      display: inline-block;
      width: .4rem;
      height: .4rem;
      margin: .1rem 0;
    }
    a{
      width: .6rem;
      margin-right: .1rem;
    }
    .git{
      background: url("../../assets/github.svg")  center no-repeat;
      background-size: cover;
    }
     .zhihu{
