      background-size: cover;
    }
    .battle{
      background: url("../../assets/Battlenet.svg")  center no-repeat;
      background-size: cover;
      &:hover{
        cursor: pointer;
      }
    }
    .steam{
      background: url("../../assets/steam.svg")  center no-repeat;
      background-size: cover;
      &:hover{
        cursor: pointer;
      }
    }
  }
}

.popover{
  width: 100px;
  height: 100px;
  background-color: #333;
}
</style>



