<template>
  <div>
     <ul class="tab">
          <li class="bread-now"  v-for=" (route , index) in routes" @mouseover="toENG(index)" @mouseout="toCHN(index)">
           <router-link :to="route.link">{{route.isMove? route.name : route.cn}}</router-link>
          </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'navBar',
  mounted () {

  },
  data () {
    return {
      routes: [
        {name: 'index', cn: '首页', link: '/', isMove: false},
        {name: 'intro', cn: '简介', link: '/intro', isMove: false},
        {name: 'record', cn: '记录', link: '/record', isMove: false},
        {name: 'contact', cn: '联系', link: '/contact', isMove: false}
      ]
    }
  },
  methods: {
    toENG (index) {
      this.routes.map(() => {
        this.routes[index].isMove = true
      })
    },
    toCHN (index) {
      this.routes.map(() => {
        this.routes[index].isMove = false
      })
    }
  }
}
</script>

<style scoped lang="scss">
@import "../../style/entry";
.tab {
  z-index: 20;
  position: fixed;
  left: .1rem;
  bottom: .4rem;
  display: flex;
  li{
    margin-right: .15rem;
    flex-direction: row;
    font-weight: 400;
    a{
      color: $color-black;
      -webkit-transition: color .5s ease;
      transition: color .5s ease;
      padding: .1rem;
      border-radius: .5rem;
      white-space: nowrap;
    }
    font-size: .2rem;
    cursor: pointer;
    a:hover{
      color: #fff;
      background: #333;
    }
  }
}
</style>



